@import url("https://use.typekit.net/jmk3xov.css");

canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 50%;
  background: var(--dark-bg);
  display: flex;
  flex-direction: column;
}
@font-face {
  font-family: "Courier";
  src: url("./public/font/CourierPrime-Regular.ttf")
  format("ttf");
  font-style: normal;
  font-weight: 400;
}
main {
  font-family: "Courier";
  width: 100vw;
  color: white;
  z-index: 99;
  position: absolute;
  width: 100%;
  margin: 0px auto;
  padding: 120px 0px;
  
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  
}

header {
  font-family: "Courier";
  border-radius: 25px;
  border: 2px solid #000000;
  background: var(--dark-bg);
  grid-column: 2 / span 6;
  font-size: 2.5vw;
  padding: 2vw;
  margin-bottom:50px;
  display: flex;
  flex-direction: column;
 
}

section {
  border-radius: 25px;
  grid-column: 2 / 8;
  padding: 0.75rem;
  background: rgba(177, 221, 241, 0.699);
  font-size: 1.75vw;
  line-height: 1.5;
  margin-bottom: 175px;
  margin-top: -150px;
  display: flex;
  flex-direction: column;
}
:root {
  --dark-bg: rgba(15, 15, 15, 1);
  --spacing: 350px;
  background: var(--dark-bg);
  font-family: "Courier";
  font-weight: 400;
  font-style: normal;
  
}
blockquote {
  margin: 0;
  padding: 0.25px;
  grid-column: 2 / span 8;
  
  margin-bottom: 150px;
  display: flex;
  margin-top: -50px;
}
img {
  border-radius: 5px;
 
}
blockquote p {
  color: black;
  border-radius: 25px;
  padding: 10px;
  border: 2px solid #000000;
  background-color: rgba(252, 203, 6, 0.637);

  font-size: 2.5vw;
  display: flex;
  
  line-height: 1;
  
}

.left {
  grid-column: 6 / 12;
}

.test {
  border-radius: 25px;
  grid-column: 2 / 8;
  padding: 0;
  background: rgba(177, 221, 241, 0);
  font-size: 1.75vw;
  line-height: 1.5;
  margin-bottom: 175px;
  margin-top: -150px;
  display: flex;
  flex-direction: column;
}
.firstblq {
  margin-bottom: 75px;
}